<template>
  <el-row>
    <el-row :gutter="12">
      <el-col :span="8">
        <el-card shadow="always">
          <i class="el-icon-s-platform"></i>服务器总数量：{{get_ip.servertotalcount}}
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card shadow="always">
          <i class="el-icon-s-platform"></i>物理机数量：{{get_ip.phycount}}
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card shadow="always">
          <i class="el-icon-s-platform"></i>虚拟机数量：{{get_ip.vircount}}
        </el-card>
      </el-col>
    </el-row>
      <el-col style="padding-top: 15px;">
        <el-tabs type="border-card" v-loading="get_loading">
          <el-tab-pane :label="title_list">
            <el-row style="padding-bottom: 8px;">
                <el-col :span="8">
                    <div class="block">
                    <span class="demonstration">选择查看日期</span>
                    <el-date-picker
                      v-model="Time"
                      align="right"
                      type="date"
                      value-format="yyyy-MM-dd"
                      placeholder="选择日期"
                      :picker-options="pickerOptions">
                    </el-date-picker>
                  </div>
                </el-col>
                <el-col :span="4">
                  <el-button type="primary" icon="el-icon-search" @click="filter_time">搜索</el-button>
                </el-col>
              <el-col :span="4">
                  <el-button type="primary" icon="el-icon-download" @click="download" :loading="get_b_loading">生成报表</el-button>
                </el-col>
                <el-col :span="5" :offset="3">
                  <div align="center">
                    <el-button type="text" @click="table = true">查看服务器详情</el-button>
                  </div>
                </el-col>
              </el-row>
            <el-row :gutter="20">
              <el-col :span="12" style="padding-bottom: 8px">
                <el-row :gutter="2">
                  <el-col :span="12">
                    <el-card class="box-card">
                  <el-alert
                      title="服务内存使用情况"
                      type="success"
                      effect="dark"
                      center
                      :closable="false">
                    </el-alert>
                    <el-col :span="12" style="padding-top: 8px;">
                      <div align="center">
                        <el-tag type="info">
                            总量M
                          </el-tag>
                      </div>
                    </el-col>
                  <el-col :span="12" style="padding-top: 8px;">
                      <div align="center">
                        <el-tag type="info">
                            使用量M
                          </el-tag>
                      </div>
                    </el-col>

                  <el-col :span="12" style="padding-top: 8px;padding-bottom: 8px;">
                      <div align="center">
                        <el-tag type="">
                            {{get_all.all.service.memorytotal}}
                          </el-tag>
                      </div>
                    </el-col>
                  <el-col :span="12" style="padding-top: 8px;padding-bottom: 8px;">
                      <div align="center">
                        <el-tag type="">
                            {{get_all.all.service.memoryuse}}
                          </el-tag>
                      </div>
                    </el-col>
                </el-card>
                  </el-col>
                  <el-col :span="12">
                    <el-card class="box-card">
                  <el-alert
                      title="大数据内存使用情况"
                      type="success"
                      center
                      effect="dark"
                      :closable="false">
                    </el-alert>
                    <el-col :span="12" style="padding-top: 8px;">
                      <div align="center">
                        <el-tag type="info">
                            总量M
                          </el-tag>
                      </div>
                    </el-col>
                  <el-col :span="12" style="padding-top: 8px;">
                      <div align="center">
                        <el-tag type="info">
                            使用量M
                          </el-tag>
                      </div>
                    </el-col>

                  <el-col :span="12" style="padding-top: 8px;padding-bottom: 8px;">
                      <div align="center">
                        <el-tag type="">
                            {{get_all.all.bigdata.memorytotal}}
                          </el-tag>
                      </div>
                    </el-col>
                  <el-col :span="12" style="padding-top: 8px;padding-bottom: 8px;">
                      <div align="center">
                        <el-tag type="">
                            {{get_all.all.bigdata.memoryuse}}
                          </el-tag>
                      </div>
                    </el-col>
                </el-card>
                  </el-col>
                </el-row>

              </el-col>
              <el-col :span="12" style="padding-bottom: 8px">
                <el-row :gutter="2">
                  <el-col :span="12">
                    <el-card class="box-card">
                  <el-alert
                      title="服务据磁盘使用情况"
                      type="warning"
                      center
                      effect="dark"
                      :closable="false">
                    </el-alert>
                    <el-col :span="12" style="padding-top: 8px;">
                      <div align="center">
                        <el-tag type="info">
                            总量G
                          </el-tag>
                      </div>
                    </el-col>
                  <el-col :span="12" style="padding-top: 8px;">
                      <div align="center">
                        <el-tag type="info">
                            使用量G
                          </el-tag>
                      </div>
                    </el-col>

                  <el-col :span="12" style="padding-top: 8px;padding-bottom: 8px;">
                      <div align="center">
                        <el-tag type="">
                            {{get_all.all.service.disktotal}}
                          </el-tag>
                      </div>
                    </el-col>
                  <el-col :span="12" style="padding-top: 8px;padding-bottom: 8px;">
                      <div align="center">
                        <el-tag type="">
                            {{get_all.all.service.diskuse}}
                          </el-tag>
                      </div>
                    </el-col>
                </el-card>
                  </el-col>
                  <el-col :span="12">
                    <el-card class="box-card">
                  <el-alert
                      title="大数据磁盘使用情况"
                      type="warning"
                      center
                      effect="dark"
                      :closable="false">
                    </el-alert>
                    <el-col :span="12" style="padding-top: 8px;">
                      <div align="center">
                        <el-tag type="info">
                            总量G
                          </el-tag>
                      </div>
                    </el-col>
                  <el-col :span="12" style="padding-top: 8px;">
                      <div align="center">
                        <el-tag type="info">
                            使用量G
                          </el-tag>
                      </div>
                    </el-col>

                  <el-col :span="12" style="padding-top: 8px;padding-bottom: 8px;">
                      <div align="center">
                        <el-tag type="">
                            {{get_all.all.bigdata.disktotal}}
                          </el-tag>
                      </div>
                    </el-col>
                  <el-col :span="12" style="padding-top: 8px;padding-bottom: 8px;">
                      <div align="center">
                        <el-tag type="">
                            {{get_all.all.bigdata.diskuse}}
                          </el-tag>
                      </div>
                    </el-col>
                </el-card>
                  </el-col>
                </el-row>
              </el-col>
              <el-col :span="12">
                <el-row :gutter="2">
                  <el-col :span="12">
                    <el-card class="box-card">
                  <el-alert
                      title="服务内存使用率"
                      type="success"
                      center
                      effect="dark"
                      :closable="false">
                    </el-alert>
                  <div align="center" style="padding-top: 8px;">
                    <el-progress type="dashboard" :percentage="get_all.all.service.memoryrate|number" :color="colors"></el-progress>
                  </div>
                </el-card>
                  </el-col>
                  <el-col :span="12">
                    <el-card class="box-card">
                  <el-alert
                      title="大数据内存使用率"
                      type="success"
                      center
                      effect="dark"
                      :closable="false">
                    </el-alert>
                  <div align="center" style="padding-top: 8px;">
                    <el-progress type="dashboard" :percentage="get_all.all.bigdata.memoryrate|number" :color="colors"></el-progress>
                  </div>
                </el-card>
                  </el-col>
                </el-row>
              </el-col>

              <el-col :span="12" >
                <el-row :gutter="2">
                  <el-col :span="12">
                   <el-card class="box-card">
                      <el-alert
                      title="服务磁盘使用率"
                      type="warning"
                      center
                      effect="dark"
                      :closable="false">
                    </el-alert>
                    <div align="center" style="padding-top: 8px;">
                      <el-progress type="dashboard" :percentage="get_all.all.service.diskrate|number" :color="colors"></el-progress>
                    </div>
                </el-card>
                  </el-col>
                  <el-col :span="12">
                   <el-card class="box-card">
                      <el-alert
                      title="大数据磁盘使用率"
                      type="warning"
                      center
                      effect="dark"
                      :closable="false">
                    </el-alert>
                    <div align="center" style="padding-top: 8px;">
                      <el-progress type="dashboard" :percentage="get_all.all.bigdata.diskrate|number" :color="colors"></el-progress>
                    </div>
                </el-card>
                  </el-col>
                </el-row>

              </el-col>
            </el-row>

          </el-tab-pane>
        </el-tabs>
      </el-col>
    <el-drawer
      title="服务器详情!"
      :visible.sync="table"
      direction="rtl"
      size="89%">
       <el-table :data="get_ip.IP" stripe border size="mini" v-loading="get_loading">
          <el-table-column property="name" label="IP" width="120"></el-table-column>
          <el-table-column property="memorytotal" label="内存总量/M"></el-table-column>
          <el-table-column property="memoryuse" label="内存使用量/M"></el-table-column>
          <el-table-column property="memoryrate" label="内存使用率" :formatter="filter_m_rate"></el-table-column>
          <el-table-column property="cpucore" label="cpu核数"></el-table-column>
          <el-table-column property="averageload" label="平均load"></el-table-column>
          <el-table-column property="disktotal" label="磁盘总量/G"></el-table-column>
          <el-table-column property="diskuse" label="磁盘使用量/G"></el-table-column>
          <el-table-column property="diskrate" label="磁盘使用率" :formatter="filter_d_rate"></el-table-column>
          <el-table-column property="servertype" label="服务器类型"></el-table-column>
          <el-table-column property="type" label="所属环境" :formatter="filterTag"></el-table-column>
        </el-table>
      <div align="center" style="padding-top: 8px;">
        <el-pagination
          @current-change="handleCurrentChange"
          background
          layout="prev, pager, next"
          :total="get_ip.servertotalcount">
        </el-pagination>
      </div>
    </el-drawer>
    </el-row>

</template>

<script>
export default {
  name: 'report',
  data () {
    return {
      msg: '报表服务',
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      Time: '',
      colors: [
          {color: '#6f7ad3', percentage: 20},
          {color: '#1989fa', percentage: 40},
          {color: '#5cb87a', percentage: 60},
          {color: '#e6a23c', percentage: 80},
          {color: '#f56c6c', percentage: 100}
        ],
      table: false,
      dialog: false,
      loading: false,
      };
  },
  methods:{
    filterTag(row, column) {
        if(row.type === 'service'){
          return "服务"
        }
        return "大数据";
      },
    download(){
      this.$store.dispatch('download',{"timestrap":this.Time});
    },
    filter_time(){
      var Json_data = this.$cookies.get("Title").split("/");
      this.$store.dispatch('get_ip_list',{"b":Json_data[0],"e":Json_data[1],"timestrap":this.Time});
      this.$store.dispatch('get_all_list',{"b":Json_data[0],"e":Json_data[1],"timestrap":this.Time});
    },
    filter_m_rate(row,column){
      return row.memoryrate + "%"
    },
    filter_d_rate(row,column){
      return row.diskrate + "%"
    },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        var Json_data = this.$cookies.get("Title").split("/");
        this.$store.dispatch('get_ip_list',{"b":Json_data[0],"e":Json_data[1],'page':val,"timestrap":this.Time});
      }
  },
  computed:{
      title_list(){
          return this.$store.state.Base.Title;
      },
      get_ip(){
        return this.$store.state.Base.Ip;
      },
      get_loading(){
        return this.$store.state.Base.loading;
      },
    get_b_loading(){
        return this.$store.state.Base.b_loading;
      },
      get_all(){
        return this.$store.state.Base.All;
      }
  },
  created(){
      var Json_data = this.$cookies.get("Title").split("/");
      this.$store.dispatch('get_ip_list',{"b":Json_data[0],"e":Json_data[1]});
      this.$store.dispatch('get_all_list',{"b":Json_data[0],"e":Json_data[1]});
      if(this.$cookies.isKey("Title")){
        this.$store.commit("set_title",this.$cookies.get("Title"))
      }
  },
}
</script>


<style scoped>

</style>



